import React from 'react';
import useMedia from '../useMedia';
import Header from "../components/Header";
import PhoneHeader from "../components/PhoneHeader";
import Footer from "../components/Footer";
import Grid from '@mui/material/Unstable_Grid2';
import {
    school,
    narita2,
    orympic,
    bellserl
  } from "../resources/img/homeGallery";

const img = {
    objectFit: "cover",
    width: "100%",
    height: "100%",
}

const imgContainer = {
    aspectRatio: "16 / 9",
}

const section = {
    paddingTop: 16,
}

const Intro = () => {
    const isMobile = useMedia('(max-width: 1000px)');

    return (
        <>
            <head>
                <meta name="viewport" content="width=device-width, initial-scale=1.0"></meta>
            </head>
            <body className={isMobile ? "phBody" : "body"}>
            <Header />
            <PhoneHeader />
            <main className={isMobile ? "main" : "phMain" }>
                <section className="title">
                    <h2 className="h2">CONTACT US</h2>
                </section>
                <div style={{padding: "12px 8px"}}>
                    <div style={{margin: "12px 0"}}>
                    <Grid container spacing={isMobile ? 1 : 2}>
                        <Grid item xs={6} style={imgContainer}><img src={school} alt="" style={img} /></Grid>
                        <Grid item xs={6} style={imgContainer}><img src={narita2} alt="" style={img} /></Grid>
                        <Grid item xs={6} style={imgContainer}><img src={orympic} alt="" style={img} /></Grid>
                        <Grid item xs={6} style={imgContainer}><img src={bellserl} alt="" style={img} /></Grid>
                    </Grid>
                    </div>
                    <div>
                        <div style={section}>
                            <p style={{borderBottom: "solid #cccccc 1px", fontSize: "20px"}}></p>
                        </div>
                        <p>//<br/>
                        </p>
                        <p></p>
                        <div style={section}>
                            <p style={{borderBottom: "solid #cccccc 1px", fontSize: "20px"}}></p>
                        </div>
                        <p>//</p>
                        <div style={section}>
                            <p style={{borderBottom: "solid #cccccc 1px", fontSize: "20px"}}></p>
                        </div>
                        <p>26</p>
                        <div style={section}>
                            <p style={{borderBottom: "solid #cccccc 1px", fontSize: "20px"}}></p>
                        </div>
                        <p>
                            63010<br />
                            <br />
                            
                        </p>
                        <p>
                            
                        </p>
                    </div>
                    <div>
                        <div style={section}>
                            <p style={{borderBottom: "solid #cccccc 1px", fontSize: "20px"}}></p>
                        </div>
                        <p>tsudoi.shamisen@gmail.com<br/>
                        080-6744-3905</p>
                    </div>
                </div>
            </main>
            </body>
            <Footer/>
        </>
    );
};

export default Intro;